body {
  font-weight: 400; }

hr {
  margin: 3rem 0;
  width: 100%;
  height: 10px;
  background-image: url("../images/article-hr.svg");
  background-repeat: repeat-x;
  background-position: center center;
  border: none; }

.off-canvas-content {
  position: relative; }
  .off-canvas-content::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    border-style: solid;
    -moz-border-image: url(../images/tree.png) 1180 1520 550 0 fill round;
    -webkit-border-image: url(../images/tree.png) 1180 1520 550 0 fill round;
    -o-border-image: url(../images/tree.png) 1180 1520 550 0 fill round;
    border-image: url(../images/tree.png) 1180 1520 550 0 fill round;
    border-width: 295px 380px 112px 0; }
    @media screen and (min-width: 33.75em) {
      .off-canvas-content::before {
        border-width: 472px 608px 180px 0; } }
    @media screen and (min-width: 47.5em) {
      .off-canvas-content::before {
        border-width: 590px 760px 225px 0; } }

.kirin {
  max-width: 1440px;
  border-image: url(../images/kirin.png) 385 50 855 980 fill round;
  border-style: solid;
  border-width: calc(385px / 4) calc(50px / 4) calc(855px / 4) calc(980px / 4);
  margin: 20px calc( ( 980px + -125px - 50px ) / -4 / 4) 0;
  position: relative;
  left: calc( (980px + -125px) / -4 / 4); }
  .kirin .kirin-inner {
    margin-left: -62.5px; }
  @media screen and (min-width: 33.75em) {
    .kirin {
      border-width: calc(385px / 3) calc(50px / 3) calc(855px / 3) calc(980px / 3);
      margin: 20px calc( ( 980px + -125px - 50px ) / -4 / 3) 0;
      left: calc( (980px + -125px) / -4 / 3); }
      .kirin .kirin-inner {
        margin-left: -83.33333px; } }
  @media screen and (min-width: 47.5em) {
    .kirin {
      border-width: calc(385px / 2) calc(50px / 2) calc(855px / 2) calc(980px / 2);
      margin: 20px calc( ( 980px + -125px - 50px ) / -4 / 2) 0;
      left: calc( (980px + -125px) / -4 / 2); }
      .kirin .kirin-inner {
        margin-left: -125px; } }
  @media screen and (min-width: 61.25em) {
    .kirin {
      border-width: calc(385px / 2) calc(50px / 2) calc(855px / 2) calc(980px / 2);
      margin: 20px calc( ( 980px + -125px - 50px ) / -4 / 2) 0;
      left: calc( (980px + -125px) / -4 / 2); }
      .kirin .kirin-inner {
        margin-left: -125px; } }

.kirin_box {
