  margin: 85px 0 20px;
  padding: 2.2em 1em 1em 1.5em; }
  @media screen and (min-width: 33.75em) {
    .kirin_box {
      padding: 3em 1.5em 1.5em 3em; } }
  .kirin_box::before {
    content: 'きりんコメント';
