/*********************
LINK STYLES
*********************/
a:link, a:visited:link {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3); }

/*********************
H1, H2, H3, H4, H5 P STYLES
*********************/
h1 a, .h1 a, h2 a, .h2 a, h3 a, .h3 a, h4 a, .h4 a, h5 a, .h5 a {
  text-decoration: none; }

h1, .h1 {
  text-align: center;
  margin: .5em 0;
  font-weight: 800;
  color: #8c6239; }
  h1::after, .h1::after {
    content: '';
    display: block;
    margin: .5em auto 0;
    height: 4px;
    width: 50%;
    background-color: #8c6239;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px; }

h2, .h2 {
  text-align: center;
  margin: 1em 0 .5em 0;
  padding: .2em;
  color: #8c6239;
  font-weight: 800;
  border-top: 2px dotted #8c6239;
  border-bottom: 2px dotted #8c6239; }

h3, .h3 {
  margin: 1em 0;
  color: #8c6239;
  font-weight: 700; }
  h3::after, .h3::after {
    content: '';
    display: block;
    margin: .3em 0;
    height: 2px;
    width: 50%;
    border-bottom: 2px dashed #8c6239; }

h4, .h4 {
  margin: .7em 0;
  color: #4a4a4a;
  font-weight: 700; }
  h4:before, .h4:before {
    content: '';
    position: relative;
    display: inline-block;
    width: 1em;
    height: 1em;
    top: .15em;
    margin-right: .5em;
    -webkit-border-radius: .5em;
    -moz-border-radius: .5em;
    border-radius: .5em;
    background-color: #8c6239; }

h5, .h5 {
  margin: 1em 0;
  letter-spacing: .2em;
  color: #8c6239;
  font-weight: 700; }
  h5:before, .h5:before {
    content: '';
    position: relative;
    display: inline-block;
    width: 1em;
    height: 1em;
    top: .15em;
    margin-right: .7em;
    -webkit-border-radius: .5em;
    -moz-border-radius: .5em;
    border-radius: .5em;
    background-color: #ec5840; }

strong.primary, b.primary {
  background-image: linear-gradient(to bottom, transparent 60%, rgba(140, 98, 57, 0.8) 60%); }

strong.secondary, b.secondary {
